body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  text-align: center;
  background-color: #D1C6AD;
}

button {
  background-color: #7dff52;

}
.App {
  text-align: center;
}


#home {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
}

h1 {
    padding-top: 1rem;
    font-size: 5rem;
    font-family: "Madimi One", sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #37cb15;
}

h2 {
    color: #37cb15;
    font-size: 2.5rem;
    text-shadow: black 1px 1px;
    font-weight: 500;
    text-wrap: balanced;
}
.title {
    font-size: 3rem;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-bottom: 2rem;
    font-weight: 600;
    color: green;
    text-align: center;
}

.miss {
    text-align: justify;
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: 2.5rem;
    font-weight: 600;
    color: green;
}

.miss_pa {
    text-align: left;
    padding-left: 4rem;
    padding-right: 4rem;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    font-size: 1.5rem;
    font-family: "Madimi One";
    font-weight: 500;
    font-style: normal;
}

.value {
    text-align: justify;
    padding-top: 2rem;
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: 2.5rem;
    font-weight: 600;
    color: green;
}

.value_con {
    text-align: justify;
    padding-left: 2rem;
    padding-right: 2rem;
    align-items: center;
}

.value_li {
    text-align: justify;
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: 1.5rem;
}

.value_pa {
    text-align: left;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    font-family: "Madimi One";
    font-weight: 500;
    font-style: normal;
}

.title_val {
    color: green;
}

.team_pa {
    text-align: left;
    padding-left: 4rem;
    padding-right: 4rem;
    font-size: 1.5rem;
    font-family: "Madimi One";
    font-weight: 500;
    font-style: normal;
}

.nav {
    align-items: center;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
}
footer {
    width: 100%;
    bottom: 0;
    left: 0;
    text-align: center;
    position: relative;
  }
